.hero {
  height: 100%;
  width: 100%;
}
.hero .content {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}
.hero .content h1 {
  font-size: 4rem;
  padding: 0.6rem 0 1.5rem;
  color: aliceblue;
}
.hero.content p {
  font-size: 1.4rem;
  font-weight: 200;
  text-transform: uppercase;
  columns: white;
}
.content .btn {
  margin: 1rem 0.2rem;
}
@media screen and (max-width: 640px) {
  .hero .content h1 {
    font-size: 3rem;
    padding: 0.6rem 0 1.5rem;
    color: aliceblue;
  }
  .hero.content p {
    font-size: 1.4rem;
  }
}
